@import '../../styles/utils/variables';

.pageContainer{
  padding-bottom:40px;
}

.container {
      width: auto;
      margin-top: 56px;
      margin-right: auto;
      padding-top:32px;
      padding-bottom: 40px;
      padding-left: 24px;
      padding-right: 24px;
      height: fit-content;
      display: flex;
      flex-direction: column;

      @media (min-width: 768px) {
        width: 752px;
        padding: 32px 32px 40px 32px;
      }
  
      @media (min-width: 1024px) {
        width: 1024px;
        margin-left: auto;
        margin-right: auto;
      }

  .pathItem{
    color:$color-text-grey;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    line-height: 1;
    padding-top: 9px;
    padding-bottom: 9px;

    .brand{
        color:$color-grey
    }
  }

  .galleryPhotosContainer{
    display: flex;
    gap:16px;
  }

  .galleryPhotosContainerRightSide::-webkit-scrollbar {
    display: none; /*    */
  }

  .galleryPhotosContainerRightSide{
    display: none;
    @media (min-width: 1024px) {
      display: flex;
      flex-direction: column;
      height: 390px;
      width: 304px;
      border-radius: 8px;
      gap: 12px;
      overflow-y: auto;
    }
  }



  .titleSection{
    padding-top: 12px;
    padding-bottom: 40px;

    @media (min-width: 768px) {
      padding-top: 0px;
    }

  .titleName{
    font-size: 22px;
    font-weight: 700;
    font-family: 'Roboto',sans-serif;
    line-height: 1.2;
    margin-bottom: 12px;

    
  }

  .titlePrice{
    font-size: 16px;
    font-weight: 600;
    font-family: 'Roboto', sans-serif;
    line-height: 1.5;
  }
  }

  .itemInfoTop{
    display:flex;
    flex-direction: column;

    @media (min-width: 768px) {
      width:768px;
      flex-direction: row;
      padding-top:24px;
    }

    @media (min-width: 1024px) {
      width: 960px;
    }
  }  

  .itemInfoTopLeft{
    width:100%;

    @media (min-width: 768px) {
      width:368px;
      margin-right: 16px;
    }

    @media (min-width: 1024px) {
      width: 640px;
    }
  }

   .mainCharacteristicsSection{
    width:100%;
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    padding-bottom: 40px;
  }


  .mainCharacteristicsItem{
    display: flex;
    align-items: center;
    width:150px;
    
    @media (min-width: 768px) {
      width:178px;
    }

    @media (min-width: 1024px) {
      width: 200px;
    }
  }




  // .mainCharacteristicsItem{
  //   display: flex;
  //   align-items: center;
  //   gap: 12px;
  // }

  .mainCharacteristicsIcon{
    width:16px;
    height: 16px;
  }

  .mainCharacteristicsText{
    font-family: 'Roboto',sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.43;
  }


  
